import classNames from "classnames";
import styles from "./presentation-step1.module.css";
import { useEffect, useState } from "react";
import { getBrowserLang, isEdge } from "@/utils";
import Image from "next/image";
import { useI18n } from "@/hooks";
type LangType = {
  title: string
}
const langs: { [key: string]: LangType } = {
  default: {
    title: ""
  },
  zh: {
    title: `将 <strong>沉浸式翻译</strong> 添加到工具栏`
  },
  en: {
    title: `Add <strong>Immersive Translation</strong> to toolbar`
  }
}
export default function PresentationStep1() {
  const i18n = useI18n<LangType>(langs)
  const [imgSrc, setImgSrc] = useState('');
  useEffect(() => {
    const lang = getBrowserLang();
    let browser = "chrome";
    if (isEdge()) {
      browser = "edge";
    }
    const gif = `/assets/${browser}_${lang}.gif`;
    setImgSrc(gif);
  }, []);
  return (
    <div className={classNames(styles.container)}>
      <h1 className={classNames(styles.title)} dangerouslySetInnerHTML={{ __html: i18n["title"] }}></h1>
      <div className={classNames("notranslate", styles.content)}>
        <Image loading={"lazy"} src={imgSrc} hidden={!imgSrc} fill={true} style={{ objectFit: 'contain' }} alt="" />
      </div>
    </div>
  );
}