
import PresentationStep1 from "@/components/presentation-step1";
import Step from "@/components/step";
import Wechat from "@/components/wechat";
import { useI18n } from "@/hooks";
import { loadResource, isFirefox, isEdge, isMobile, isSafari } from "@/utils";
import { base_url } from "@/utils/const";
import classNames from "classnames";
import Head from "next/head";
import { CSSProperties, useEffect, useState } from "react";

const nextUrl = base_url + "/step-2/";
type LangType = {
  title: string,
  subtitle: string,
  pin: string,
  next: string
}
const langs: { [key: string]: LangType } = {
  default: {
    title: "",
    subtitle: "",
    pin: "",
    next: "",
  },
  zh: {
    title: "沉浸式翻译 - 固定插件",
    subtitle: "✋ 快速开始体验沉浸式翻译",
    next: "我已完成，开始翻译",
    pin: "将”沉浸式翻译“扩展添加至工具栏"
  },
  en: {
    title: "Immersive Translation - Fixed Plugin",
    subtitle: "✋ Quickly Start Experience Immersive Translation",
    next: "I'm done, start translating",
    pin: "Add the Immersive Translation extension to the toolbar"
  }
}
export default function Step1() {
  const i18n = useI18n<LangType>(langs);
  usePingUrl();
  const style = usePin();
  return (
    <main>
      <Head>
        <title>{i18n["title"]}</title>
      </Head>
      <div className={classNames("card")}>
        <h1 className={classNames("title", "notranslate")}>{i18n["subtitle"]}</h1>
        <Step step={1} />
        <PresentationStep1 />
        <footer className={classNames("notranslate", "flex", "justify-center")}>
          <a className={classNames("ghost")} href={nextUrl}> {i18n["next"]}</a>
          <link rel="prefetch" href={nextUrl} />
        </footer>
      </div>
      <Wechat />
      <div className={classNames("pin-extension")} style={style}>
        {i18n["pin"]}
      </div>
      <style jsx>{`
  .pin-extension {
    position: absolute;
    right: 80px;
    top: 0;
    width: 250px;
    padding-top: 64px;
    background-position: right top;
    background-repeat: no-repeat;
  }
      `}</style>
    </main>
  );
}

function usePingUrl() {
  useEffect(() => {
    const url = "https://www.kadaza.com/";
    loadResource(url).catch((e) => {
      console.error(e);
      (window as any).dataLayer?.push({ event: "ping_fail", moreSite: url });
    });
  }, []);
}
function usePin() {
  const [style, setStyle] = useState<CSSProperties>({ display: 'none' });
  useEffect(() => {
    if (isMobile()) return;
    const pin = document.querySelector(".pin-extension") as HTMLElement;
    let browser = "chrome";
    let curStyle = { display: 'none' } as CSSProperties;
    //safari 不显示 userAgent
    if (pin && !isSafari()) {
      curStyle["display"] = 'block';
    }
    if (isFirefox()) {
      curStyle['right'] = '20px';
    }
    if (isEdge()) {
      browser = "edge";
      curStyle['backgroundPosition'] = '115px 0';
    }
    curStyle['backgroundImage'] = `url(${base_url}/assets/pin-extension-${browser}.svg)`;
    setStyle(curStyle);
  }, [setStyle]);
  return style;
}